.container {
  padding: 0;
  font-family: "M PLUS 1P";
}

.MuiPaper-elevation {
  justify-content: center;
}

.cardImg {
  height: 200px;
  width: 50%;
  object-fit: cover;
  transition: all 0.2s;
}

html,
body {
  padding: 0;
  margin: 0;
  font-family: "M PLUS 1P", -apple-system, BlinkMacSystemFont, Segoe UI, Roboto,
    Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
  background: rgb(243, 243, 243);
  color: #333;
}

a {
  color: inherit;
  text-decoration: none;
}

* {
  box-sizing: border-box;
}

p,
h1,
h2,
h3,
h4,
h5,
h6,
label,
span,
button {
  padding: 0;
  margin: 0;
  transform: rotate(0.05deg);
  line-height: 1.5em;
}

p,
label {
  font-weight: 500;
}

h2 {
  margin: 0;
}

@media (prefers-color-scheme: dark) {
  html {
    color-scheme: dark;
  }

  body,
  html {
    color: rgb(225, 225, 225);
    background: rgb(30, 30, 30);
  }
}
