.filterPanel {
  padding: 0 1rem;
}

.filterPanel input {
  margin: 0;
}

.filterPanel .category {
  padding: 0.8rem 0;
}

.filterPanel input {
  display: none;
}

.filterPanel .category:not(:nth-of-type(1)) {
  border-top: 1px solid rgb(222, 222, 222);
}

.filterPanel h3 {
  margin-bottom: 1rem;
}

.filterPanel h4 {
  margin: 0.5rem 0 0.3rem;
}

.filterGrid {
  display: grid;
  align-items: center;
}

.vocal {
  grid-template-columns: 115px repeat(auto-fill, minmax(85px, 1fr));
}

.otherVocal {
  grid-template-columns: 115px repeat(auto-fill, minmax(125px, 1fr));
}

.vocalUnit {
  grid-row: 1 / 10;
}

.button {
  display: flex;
}

.filterPanel p {
  font-size: 14px;
}

.unit {
  grid-template-columns: repeat(auto-fill, minmax(115px, 1fr));
}

.filterCategoryTitle {
  display: grid;
  grid-template-columns: max-content auto;
  align-items: center;
  gap: 5px;
}

.filterCategoryTitle p {
  display: inline;
  background: #00ccbb;
  border-radius: 14px;
  color: #fff;
  padding: 2px 24px;
  align-items: center;
}

.divider {
  height: 10px;
  width: 100%;
  background-image: radial-gradient(
    circle farthest-side,
    #00ccbb 10%,
    transparent 35%
  );
  background-size: 15px 10px;
}

.filterFlex {
  display: flex;
  gap: 10px;
}

.sort {
  grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
}

.group {
  grid-template-columns: repeat(auto-fill, minmax(80px, 1fr));
}

.confirmButton,
.cancelButton {
  border: 2px solid #555577;
  border-bottom: 3px solid #555577;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.confirmButton {
  background: #fff;
}

.cancelButton {
  background: #e1e1e1;
}

.confirmButton > div,
.cancelButton > div {
  width: calc(100% - 8px);
  height: calc(100% - 8px);
  border-radius: 4px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.confirmButton > div {
  background: linear-gradient(#01ffc2, #00fff9);
}

.cancelButton > div {
  background: #fff;
}

.confirmButton p,
.cancelButton p {
  font-size: 18px;
  color: #444466;
  font-weight: 550;
}

.confirmButton .dot,
.cancelButton .dot {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 7px;
  background-size: 9.8px 9.8px;
  background-position: -4.5px -4.9px, 0.4px 0;
}

.confirmButton .dot {
  background-image: radial-gradient(
      circle farthest-side,
      rgba(255, 255, 255, 0.7) 35%,
      transparent 55%
    ),
    radial-gradient(
      circle farthest-side,
      rgba(255, 255, 255, 0.7) 30%,
      transparent 45%
    );
}

.cancelButton .dot {
  background-image: radial-gradient(
      circle farthest-side,
      rgb(237, 237, 237) 35%,
      transparent 55%
    ),
    radial-gradient(
      circle farthest-side,
      rgb(237, 237, 237) 30%,
      transparent 45%
    );
}

.confirmButton .dot::before,
.cancelButton .dot::before {
  content: "";
  position: absolute;
  top: 7px;
  left: 0;
  width: 100%;
  height: 9px;
  background-image: radial-gradient(
      circle farthest-side,
      rgba(255, 255, 255, 0.7) 25%,
      transparent 40%
    ),
    radial-gradient(
      circle farthest-side,
      rgba(255, 255, 255, 0.7) 17%,
      transparent 32%
    );
  background-size: 9.8px 9.8px;
  background-position: -4.5px -2.5px, 0.4px 2.4px;
}

.confirmButton .dot::before {
  background-image: radial-gradient(
      circle farthest-side,
      rgba(255, 255, 255, 0.7) 25%,
      transparent 40%
    ),
    radial-gradient(
      circle farthest-side,
      rgba(255, 255, 255, 0.7) 17%,
      transparent 32%
    );
}

.cancelButton .dot::before {
  background-image: radial-gradient(
      circle farthest-side,
      rgb(237, 237, 237) 25%,
      transparent 40%
    ),
    radial-gradient(
      circle farthest-side,
      rgb(237, 237, 237) 17%,
      transparent 32%
    );
}

.confirmButton .dot::after,
.cancelButton .dot::after {
  content: "";
  position: absolute;
  top: 16px;
  left: 0;
  width: 100%;
  height: 9px;
  background-image: radial-gradient(
    circle farthest-side,
    rgba(255, 255, 255, 0.7) 7%,
    transparent 17%
  );
  background-size: 9.8px 9.8px;
  background-position: -4.5px -1.5px;
}

.confirmButton .dot::after {
  background-image: radial-gradient(
    circle farthest-side,
    rgba(255, 255, 255, 0.7) 7%,
    transparent 17%
  );
}

.cancelButton .dot::after {
  background-image: radial-gradient(
    circle farthest-side,
    rgb(237, 237, 237) 7%,
    transparent 17%
  );
}
